import React, { Component } from 'react';
import { View, StyleSheet, Text, ScrollView, Dimensions, TouchableOpacity, Image } from 'react-native';
import { Router, Switch, Route, Link } from './routing/routing';
import {withRouter} from 'react-router'

import HomeScreen from "./screens/HomeScreen.js"
import SearchScreen from "./screens/SearchScreen.js"
import SavedEventsScreen from "./screens/SavedEventsScreen.js"
import DetailsScreen from "./screens/DetailsScreen.js"
import MoreScreen from "./screens/MoreScreen.js"
import MyTabBar from "./components/MyTabBar.js"

import Icon from 'react-native-vector-icons/Ionicons'



const styles = StyleSheet.create({
  container:{
    flex:1,
    height: Dimensions.get("window").height
  },
  header:{
    backgroundColor: '#EEE',
    borderBottomColor: '#f16522',
    borderBottomWidth: 8,
    elevation: 10,
    height: '60px',
    justifyContent: 'center',
    alignItems: 'center',
  },
  headerTitle:{
    textAlign:"center",
    fontSize: '25px',
    fontWeight: 'bold',
    color: "#555"
  },
  headerImage:{
    width: "200px",
    height: '45px',
    resizeMode: 'contain'
  },
  bottomTabBar:{
    height: '50px',
    flexDirection:'row',
    justifyContent: 'space-around',
    alignItems: 'center',
  },
  button: {
    flex: 1,
    alignItems: 'center'
  },
  innerrouter:{
    flex:1,
    justifyContent: 'flex-start'
  },
})

export default class AppStart extends Component
{

  render(){
    return(
      <View style={styles.container}>
        <Router style={styles.innerrouter}>
          <View style={styles.header}>
            <Image source={require("./assets/logores.png")} style={styles.headerImage}/>
          </View>
          <Switch>
            <Route exact path="/web" render={props => <HomeScreen {...props}/>} />
            <Route path="/search" render={props => <SearchScreen {...props}/>} />
            <Route path="/savedevents" render={props => <SavedEventsScreen {...props}/>} />
            <Route path="/appinfo" render={props => <MoreScreen {...props}/>} />
            <Route path="/details/:id" render={props => <DetailsScreen {...props}/>} />
          </Switch>
          <View style={styles.bottomTabBar}>
            <HomeButton />
            <SearchButton/>
            <HeartButton style={styles.button}/>
            <MoreButton style={styles.button}/>
          </View>
        </Router>
      </View>
    )}
}

const btn = {
active: '#FF9A46',
inactive: '#AAA'
}

const MoreButton = withRouter(({ history, location}) => {
  var route = "/appinfo"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-more' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

const HeartButton = withRouter(({ history, location}) => {
  var route = "/savedevents"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-heart' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

const HomeButton = withRouter(({ history, location}) => {
  var route = "/web"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-home' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

const SearchButton = withRouter(({ history, location}) => {
  var route = "/search"
  return(<TouchableOpacity style={styles.button} onPress={() => {history.push(route)}}>
    <Icon name='md-search' type="ionicon" size={30} color={route == location.pathname ? btn.active : btn.inactive} />
  </TouchableOpacity>)
  }
)

class BarButton extends Component {
  render() {
    return (<Icon name={this.props.name} type="ionicon" size={30} color={'#AAA'} />)
  }
}
