import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  FlatList,
  TouchableOpacity,
  Button,
  ScrollView,
  SectionList,
  Picker
} from 'react-native';

import {withRouter} from 'react-router'


import tempevents from "../tempdata/TempDataFile.js"
import EventElement from "../components/EventElement.js"
import DetailsComponent from "../components/DetailsComponent.js"
import FeaturedEvents from "../components/FeaturedEvents.js"
import FilterButtons from "../components/FilterButtons.js"

export default class HomeScreen extends Component {
  constructor(props)
  {
    super(props)
    this.state =
    {
      events: null,
      option: 1, //0 for past, 1 for events today, 2 for future
      eventstorender: [],
      dividedevents: [],
      modalvisible: false,
      isFetching: false
    }
  }

  fetchData(){
    fetch("https://hangouter-backend.herokuapp.com/api/v1/events")
    .then(response => response.json())
    .then((responseJson)=> {
      this.setState({
       events: this.sortEvents(responseJson)
      })
      this.setState({
        dividedevents: this.processEvents(this.state.events),
      })
      this.setState({
        eventstorender: this.state.dividedevents[this.state.option].data
      })

      this.setState({isFetching: false})
    })
    .catch(error=>console.log(error)) //to catch the errors if any
  }

  processEvents(events){
    if (!events)
      return []
    var sectiondata = [
      {
        title: "Past Events",
        data: []
      },
      {
        title: "Today",
        data: []
      },
      {
        title: "Future Events",
        data: []
      },
      ];

    events.forEach(function (item, index) {
      var start = new Date(item.starttime)
      var end = item.endtime ? new Date(item.endtime) : null
      var today = new Date()
      if (HomeScreen.isActive(item)){
          if (end){
            start.setHours(0);
            start.setMinutes(0);
            end.setHours(0);
            end.setMinutes(0);
            if (start <= today && today <= end)
              sectiondata[1].data.push(item)
            else
              sectiondata[0].data.push(item)
          }else{
            start.setHours(0);
            start.setMinutes(0);
            if (start==today)
              sectiondata[1].data.push(item)
            else
            sectiondata[0].data.push(item)
          }
      }else{
        sectiondata[2].data.push(item)
      }
    });
    return sectiondata
  }
  sortEvents(events){
    return events.sort((a,b) => {
      var adate = new Date(a.starttime)
      var bdate = new Date(b.starttime)
      if (adate > bdate){
        return -1
      }else if (adate==bdate){
        return 0
      }else{
        return 1
      }
    })
    return events
  }

  componentDidMount(){
    this.fetchData()
  }

  onRefresh() {
   this.setState({ isFetching: true }, function() { this.fetchData() });
  }

  static isActive(event){
    var active = true
    if (event.endtime){
      if (new Date() > new Date(event.endtime))
        active = false
    }else{
      var enddate = new Date(event.starttime)
      enddate.setHours(0);
      enddate.setMinutes(0);
      enddate.setDate(enddate.getDate() + 1)
      if (new Date() > enddate)
        active = false
    }
    return true
    //return active
  }

  _flatListRenderer({item}){
    var Element = withRouter(({history}) => (
      <TouchableOpacity onPress={() => {history.push("/details/" + item._id, {item: item})}}>
          <EventElement active={HomeScreen.isActive(item)} item={item}/>
      </TouchableOpacity>))
    return ( <Element/> )
  }

  _sectionHeaderRenderer({ section: { title }}){
    return(
    <Text style={styles.sectionheader}>{title}</Text>)
  }

  _optionChanged(option)
  {
    this.setState({
      option: option,
      eventstorender: this.state.dividedevents[option].data
    })
    console.log(option)
    console.log(this.state.dividedevents)
    console.log(this.state.eventstorender)
  }

  render() {
    console.log("RENDERING")
    console.log(this.state.eventstorender)
    return (
      <View style={styles.container}>
        <ScrollView>
        <View style={styles.featured}>
          <FeaturedEvents/>
        </View>

        <FilterButtons onChange={this._optionChanged.bind(this)}/>

        <FlatList style={styles.flatliststyle}
        data={this.state.eventstorender}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item._id}
        />

        </ScrollView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container:{
    flex: 1,
    justifyContent: 'center',
    alignItems: 'stretch',
    backgroundColor: '#FFF'
  },
  featured: {
    height: 200,
  },
  title:{
    color: '#000',
    fontSize: 30,
  },
  flatliststyle:{
    flex: 5,
  },
  sectionheader:{
    textAlign: 'center',
    fontWeight: 'bold',
    color: '#777',
    flex: 1,
    fontSize: 20,
    justifyContent: 'center',
  },
});
