import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableWithoutFeedback,
  TouchableOpacity
} from 'react-native';

const styles = StyleSheet.create({
    button: {
      flex:1,
      alignItems: 'center',
      justifyContent: 'center',
      alignItems: 'center',
      borderRadius: 10,
      backgroundColor: "#FAA"
    },
    buttons: {
      height: 30,
      flexDirection: 'row',
    },
    textstyle: {
      fontSize: 17,
      fontWeight: 'bold',
      textAlign: 'center',
      color: "#777"
    }});

export default class FilterButtons extends Component{
  constructor(props)
  {
    super(props)
    this.state = {
      option: 1
    };
    this.activec = "#FF9A46"
    this.inactivec = "#FFF"
  }

  changeOption(option)
  {
    this.setState({option: option})
    this.props.onChange(option)
  }

  render(){
    return(
      <View style={styles.buttons}>
        <TouchableOpacity style={[styles.button, {backgroundColor: this.state.option===0 ? this.activec : this.inactivec}]}
        onPress={() => {this.changeOption(0)}}>
          <Text style={styles.textstyle}> Past Events </Text>
        </TouchableOpacity>
        <TouchableOpacity style={[styles.button, {backgroundColor: this.state.option===1 ? this.activec : this.inactivec}]}
        onPress={() => {this.changeOption(1)}}>
          <Text style={styles.textstyle}> Today </Text>
        </TouchableOpacity>
        <TouchableOpacity style={[styles.button, {backgroundColor: this.state.option===2 ? this.activec : this.inactivec}]}
        onPress={() => {this.changeOption(2)}}>
          <Text style={styles.textstyle}> Tomorrow </Text>
        </TouchableOpacity>
      </View>
    )
  }
}
