import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  FlatList,
  TouchableWithoutFeedback,
  TouchableOpacity,
  ImageBackground
} from 'react-native';

import {tempfeaturedevents} from "../tempdata/TempDataFile.js"

const styles = StyleSheet.create({
  maincontainer: {
    marginLeft: 20,
    marginVertical: 15,
    flex: 1,
    justifyContent: 'center',
    alignItems: 'stretch'
  },
  eventtitle: {
    fontSize: 25,
    fontWeight: 'bold',
    color: '#777',
    backgroundColor: '#DDDD'
  },
  flatliststyle: {
    flex: 1
  },
  featimagestyle: {
    justifyContent: 'flex-end',
    flex: 1,
    width: 340,
    height: 170,
    marginRight: 15,
    //resizeMode: 'contain' TODO DOES NOT WORK ON WEB
  }
})

export default class FeaturedEvents extends Component{

  _flatListRenderer({item}){
    return(
      <ImageBackground source={{uri: item.backgroundImage}}
          style={styles.featimagestyle}>
        <Text style={styles.eventtitle}>
          {item.title}
        </Text>
      </ImageBackground>
    )
  }

  getSnappingWidth(){
    // FIXME magic number, 340 is width of list element, 15 space between them
    var snappingWidth = 340 + 15
    return snappingWidth
  }

  render(){
    return(
      <View style={styles.maincontainer}>
        <FlatList style={styles.flatliststyle}
        data={tempfeaturedevents}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item.id}
        horizontal={true}
        snapToInterval={this.getSnappingWidth()}
        />
      </View>
    )
  }
}
