import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableOpacity
} from 'react-native';

import {Icon} from 'react-native-elements';
import IconF from 'react-native-vector-icons/FontAwesome'

import EventElement from './EventElement'

const styles = StyleSheet.create({
  maincontainer: {
    flexDirection:'row',
    backgroundColor:'#FFF',
    height:170,
    marginTop: 15,
    marginBottom: 15,
    paddingVertical: 15,
    shadowOffset:{width:1,height:1},
    shadowColor: '#CCC',
    shadowOpacity: 1.0,
    shadowRadius: 1
  },
  itemcontainer: {
    flex: 5,
    flexDirection: 'row',
    justifyContent: "flex-end"
  },
  thumbnail: {
    flex: 1,
    width: undefined,
    height: undefined,
  },
  textcontainer: {
    flex: 4,
    marginLeft: 10,
    justifyContent: 'space-between'
  },
  title: {
    fontSize: 25,
    fontWeight: 'bold',
    color: '#777',
  },
  place: {
    fontSize: 20,
    marginTop:2.5,
    color: "#555"
  },
  date: {
    fontSize: 20,
    marginTop:2.5,
    color: "#555"

  },
  time: {
    fontSize: 20,
    marginTop:2.5,
    color: "#555"

  },
  icon: {
    width: 23,
    height: 23,
    tintColor: '#555'
  },
  texticon: {
    height: 14,
    width: 20,
    tintColor: '#555',
    resizeMode: 'contain'
  },

  category: {
    marginRight: 15,
    paddingRight: 15
  },

  placedatetime: {
    justifyContent: "flex-end"
  }


})

// This component should display basic details
// that is, Title, Place, Date and Time
export default class DetailsComponent extends Component{

  render(){
    const {item} = this.props
    return(
      <View style={styles.maincontainer}>
          <Image source={{uri: this.props.item.thumbnail}}
                             style={styles.thumbnail}
                             resizeMode="contain" />
          <View style={styles.textcontainer}>
            <Text style={styles.title}>
                          {EventElement.renderCategoryIcon(this.props.item.category)}
                          {this.props.item.title}
            </Text>
          <View style={styles.placedatetime}>
            <Text style={styles.place}>
              <IconF name="map-marker" size={18}/>
              <Text>  {this.props.item.place} </Text>
            </Text>

            <Text style={styles.date}>
              <IconF name="calendar-o" size={18}/>
              <Text>  {EventElement.getDateString(this.props.item.starttime, this.props.item.endtime)} </Text>
            </Text>

            <Text style={styles.time}>
              <IconF name="clock-o" size={18}/>
              <Text>  {EventElement.getTimeString(this.props.item.starttime, this.props.item.endtime)}</Text>
            </Text>
          </View>
        </View>
      </View>
    )
  }

}
